.footer {
    display: flex;
    justify-content: center;
    margin-top: 160px;
    background-color: $bg-dark;
    padding: 48px 64px 128px;
    border-radius: 20px;

    @include less-than('lg') {
        margin-top: 60px;
    }

    &__logo {
        width: 100%;
    }

    position: relative;

    @include less-than('md') {
        &>.corner {
            display: none;
        }
    }
}